import { useEffect } from "react";
import styles from "./Blog.module.css";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { fetchBlogsAsync, selectAllBlogs, selectBlogStatus } from "./BlogSlice";
import { FidgetSpinner, Audio, Vortex } from "react-loader-spinner";

function Blog() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const blogs = useSelector(selectAllBlogs);
  const status = useSelector(selectBlogStatus);
  useEffect(() => {
    dispatch(fetchBlogsAsync());
  }, [dispatch]);

  return (
    <div className={styles.blogsWrapper}>
      {status === "loading" ? (
        <Vortex
          visible={true}
          height="160"
          width="160"
          ariaLabel="vortex-loading"
          wrapperStyle={{}}
          wrapperClass="vortex-wrapper"
          colors={["red", "green", "blue", "yellow", "orange", "purple"]}
        />
      ) : null}
      {blogs &&
        blogs.map((blog) => (
          <div
            id={blog._id}
            className={styles.blog}
            onClick={() => navigate(`/blog/${blog._id}`)}
          >
            <h1>{blog.title}</h1>
            <img src={blog.photoPath} alt="error" />
            <p>{blog.content}</p>
          </div>
        ))}
    </div>
  );
}

export default Blog;
