import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { fetchBlogs } from "./BlogApi";

const initialState = {
  blogs: [],
  status: "idle",
};

export const fetchBlogsAsync = createAsyncThunk(
  "Blog/fetchAllBlogs",
  async () => {
    const response = await fetchBlogs();
    console.log(response.data);
    return response.data;
  }
);

export const BlogSlice = createSlice({
  name: "Blog",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(fetchBlogsAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchBlogsAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.blogs = action.payload;
      });
  },
});

export const selectAllBlogs = (state) => state.Blog.blogs;
export const selectBlogStatus = (state) => state.Blog.status;

export default BlogSlice.reducer;
