import { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import styles from "./UpdateBlog.module.css";
import { useSelector } from "react-redux";
import { selectUserInfo } from "../User/userSlice";
import axios from "axios";
import { useAlert } from "react-alert";

function UpdateBlog() {
  const navigate = useNavigate();
  const alert = useAlert();

  const params = useParams();
  const blogId = params.id;
  const userInfo = useSelector(selectUserInfo);

  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [photoPath, setPhotoPath] = useState("");

  const updateHandler = async () => {
    let data = {
      author: userInfo.id,
      title,
      content,
      photoPath,
      blogId,
    };
    try {
      const response = await axios.put(`/blog`, data);

      if (response.status === 200) {
        alert.show("Blog updated");
        navigate("/");
      }
    } catch (e) {
      alert.error("something wrong");
    }
  };

  useEffect(() => {
    async function getBlogDetails() {
      const response = await axios.get(`/blog/${blogId}`);
      if (response.status === 200) {
        setTitle(response.data.blog.title);
        setContent(response.data.blog.content);
        setPhotoPath("");
      }
    }
    getBlogDetails();
  }, [blogId]);

  return (
    <div className={styles.wrapper}>
      <div className={styles.header}>Edit your blog</div>
      <input
        type="text"
        name="title"
        placeholder="title"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        style={{ width: "60%" }}
      />
      <textarea
        className={styles.content}
        placeholder="your content goes here..."
        maxLength={400}
        value={content}
        onChange={(e) => setContent(e.target.value)}
      />
      <input
        type="url"
        className={styles.url}
        name="photoPath"
        value={photoPath}
        onChange={(e) => setPhotoPath(e.target.value)}
      />
      <button className={styles.update} onClick={updateHandler}>
        Update
      </button>
    </div>
  );
}

export default UpdateBlog;
