import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
import { selectLoggedInUser } from "../pages/auth/authSlice";
import styles from "./navbar.module.css";
import { selectUserInfo } from "../pages/User/userSlice";
import { useRef } from "react";

function NavBar() {
  const user = useSelector(selectLoggedInUser);
  const userInfo = useSelector(selectUserInfo);

  const navButton = useRef(null);
  const linksContainerRef = useRef(null);

  function collapseNav() {
    navButton.current.classList.add("collapsed");
    linksContainerRef.current.classList.remove("show");
  }
  return (
    <>
      <nav class="navbar navbar-expand-lg navbar-light bg-grey-50">
        <div class="container-fluid">
          <NavLink
            class="navbar-brand"
            to="/"
            className={`${styles.logo} ${styles.inactiveStyle}`}
          >
            BLOG-APP
          </NavLink>

          <button
            ref={navButton}
            class="navbar-toggler bg-white"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>
          <div
            ref={linksContainerRef}
            class="collapse navbar-collapse"
            id="navbarNav"
          >
            <ul class="navbar-nav">
              <li class="nav-item py-3">
                <NavLink
                  onClick={collapseNav}
                  class="nav-link active"
                  aria-current="page"
                  to="/"
                  className={({ isActive }) =>
                    isActive ? styles.activeStyle : styles.inactiveStyle
                  }
                >
                  Home
                </NavLink>
              </li>

              <li class="nav-item py-3">
                <NavLink
                  onClick={collapseNav}
                  to="submit"
                  className={({ isActive }) =>
                    isActive ? styles.activeStyle : styles.inactiveStyle
                  }
                >
                  Submit a blog
                </NavLink>
              </li>

              {!user ? (
                <>
                  <li class="nav-item ">
                    <NavLink
                      onClick={collapseNav}
                      to="login"
                      className={({ isActive }) =>
                        isActive ? styles.activeStyle : styles.inactiveStyle
                      }
                    >
                      <button className={styles.login}>Log In</button>
                    </NavLink>
                  </li>
                  <li class="nav-item">
                    <NavLink
                      onClick={collapseNav}
                      to="signup"
                      className={({ isActive }) =>
                        isActive ? styles.activeStyle : styles.inactiveStyle
                      }
                    >
                      <button className={styles.signup}>Sign Up</button>
                    </NavLink>
                  </li>
                </>
              ) : (
                <li class="nav-item ">
                  {userInfo && <span>{userInfo.email}</span>}
                  <NavLink
                    onClick={collapseNav}
                    to="signOut"
                    className={({ isActive }) =>
                      isActive ? styles.activeStyle : styles.inactiveStyle
                    }
                  >
                    <button className={styles.signout}>Sign Out</button>
                  </NavLink>
                </li>
              )}
            </ul>
          </div>
        </div>
      </nav>
      <div className={styles.separator}></div>
    </>
  );
}
export default NavBar;
