import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import styles from "./Blog-Detail.module.css";
import CommentList from "../../components/Comment/CommentList";
import { selectUserInfo } from "../User/userSlice";
import { selectLoggedInUser } from "../auth/authSlice";
import axios from "axios";
import { useAlert } from "react-alert";

function BlogDetails() {
  const alert = useAlert();

  const [blog, setBlog] = useState([]);
  const [comments, setComments] = useState([]);
  const [ownsBlog, setOwnsBlog] = useState(false);
  const [newComment, setNewComment] = useState("");
  const [reload, setReload] = useState(false);
  const userInfo = useSelector(selectUserInfo);
  const user = useSelector(selectLoggedInUser);

  const navigate = useNavigate();

  const params = useParams();
  const blogId = params.id;

  useEffect(() => {
    async function getBlogDetails() {
      const commentResponse = await axios.get(
        `/comment/${blogId}`
      );
      if (commentResponse.status === 200) {
        setComments(commentResponse.data);
      }

      const blogResponse = await axios.get(
        `/blog/${blogId}`
      );
      if (blogResponse.status === 200) {
        // set ownership
        setOwnsBlog(
          userInfo && userInfo.name === blogResponse.data.blog.authorName
        );
        setBlog(blogResponse.data.blog);
      }
    }
    getBlogDetails();
  }, [reload]);

  const postCommentHandler = async () => {
    const data = {
      author: userInfo.id,
      blog: blogId,
      content: newComment,
    };

    const response = await axios.post(`/comment`, data);
    if (response.status === 201) {
      setNewComment("");
      setReload(!reload);
    }
  };

  const deleteBlogHandler = async () => {
    try {
      const response = await axios.delete(
        `/blog/${blogId}`
      );

      if (response.status === 200) {
        alert.show("Blog deleted");
        navigate("/");
      }
    } catch (e) {
      alert.error("Something wrong");
    }
  };

  return (
    <div className={styles.detailsWrapper}>
      <div className={styles.left}>
        <h1 className={styles.title}>{blog.title}</h1>
        <div className={styles.meta}>
          <p>
            @
            {blog.authorName + " on " + new Date(blog.createdAt).toDateString()}
          </p>
        </div>
        <div className={styles.photo}>
          <img src={blog.photo} width={250} height={250} />
        </div>
        <p className={styles.content}>{blog.content}</p>
        {ownsBlog && (
          <div className={styles.controls}>
            <button
              className={styles.editButton}
              onClick={() => {
                navigate(`/blog-update/${blog._id}`);
              }}
            >
              Edit
            </button>
            <button className={styles.deleteButton} onClick={deleteBlogHandler}>
              Delete
            </button>
          </div>
        )}
      </div>
      <div className={styles.right}>
        <div className={styles.commentsWrapper}>
          <CommentList comments={comments} />
          {userInfo && (
            <div className={styles.postComment}>
              <input
                className={styles.input}
                placeholder="comment goes here..."
                name="content"
                value={newComment}
                onChange={(e) => setNewComment(e.target.value)}
              />
              <button
                className={styles.postCommentButton}
                onClick={postCommentHandler}
              >
                Post
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default BlogDetails;
